<template>
  <TopBar
    ref="topBar"
    class="d-none d-md-flex"
    :navItemsHeight="navItemsHeight"
  />

  <Home />
  <Aboutme />
  <Portfolio />
  <transition name="showBar">
    <BottomBar
      ref="bottomBar"
    
      :navItemsHeight="navItemsHeight"
      :class="barStatus"
    />
  </transition>
  <transition name="showButton">
    <BottomButton
    
      @change-status="showBar"
      :class="buttonStatus"
    />
  </transition>
  <div class="bg-white d-md-none" style="height: 70px"></div>
</template>

<script lang="ts">
import Home from "./views/Home.vue";
import Aboutme from "./views/Aboutme.vue";
import BottomBar from "./components/BottomBar.vue";
import BottomButton from "./components/BottomButton.vue";
import TopBar from "./components/TopBar.vue";
import { defineComponent } from "vue";
import Portfolio from "./views/Portfolio.vue";
// import {i18n} from "../src/main"

let flag = true;
function throttle(fn: any, delay: number) {
  return function () {
    if (flag) {
      setTimeout(() => {
        fn();
        flag = true;
      }, delay);
    }
    flag = false;
  };
}

function debounce(fn: any, delay: number) {
  let t: any = null;
  return function () {
    // 若已经开始执行了
    if (t != null) {
      clearTimeout(t);
    }
    // 重新开始
    t = setTimeout(() => {
      fn();
      flag = true;
    }, delay);
  };
}

export default defineComponent({
  components: {
    TopBar,
    BottomBar,
    BottomButton,
    Home,
    Aboutme,
    Portfolio,
 
  
  },
  data() {
    return {
      barStatus: "d-none",
      buttonStatus: "",
      navItemsHeight: [0, 0, 0],
      navItems: ["nav-item-1", "nav-item-2", "nav-item-3"],
    };
  },

  methods: {
    showBar() {
        console.log("绑定事件")
      this.buttonStatus = "d-none";
      this.barStatus = "";
    },
    hideBar(e: Event) {
      if (this.barStatus=="") {
        // 当bar存在的时候
        const target = e.target as HTMLElement;
        const targetParent = target.parentElement as HTMLElement;
        if (
          targetParent?.className?.indexOf("bottom-bar-item") < 0 &&
          target?.className?.indexOf("bottom-bar") < 0
        ) {
          this.barStatus = "d-none";
          this.buttonStatus = "";
        }
      }
    },
    scrollEvent() {
      const currentHeight = document.documentElement.scrollTop as number;
      let index = 0;

      //循环判断当前位置
      for (let i = 0; i < this.navItemsHeight.length; i++) {
        if (currentHeight < this.navItemsHeight[i] - 100) {
          index = i - 1 < 0 ? 0 : i - 1;
          break;
        } else if (!isNaN(this.navItemsHeight[i])) {
          index = i;
        }
      }
      const targetId = this.navItems[index];

      (this.$refs.topBar as any).selectedId =targetId ;

      console.log(this.$refs.bottomBar);
      console.log(targetId);
      (this.$refs.bottomBar as any).selectedId = targetId;
    
      // console.log(this.$refs.topBar.selectedId=)
    },
  },

  mounted() {
  
    this.$data.navItemsHeight = [
      (document.getElementById("home")?.offsetTop as number) - 70 < 0
        ? 0
        : (document.getElementById("home")?.offsetTop as number) - 70,
      (document.getElementById("aboutme")?.offsetTop as number) - 70 < 0
        ? 0
        : (document.getElementById("aboutme")?.offsetTop as number) - 70,
      (document.getElementById("portfolio")?.offsetTop as number) - 70 < 0
        ? 0
        : (document.getElementById("portfolio")?.offsetTop as number) - 70,
    ];

    // 监听滚动条事件
    document.addEventListener("scroll", debounce(this.scrollEvent, 100));
    // 监听点击事件
    document.addEventListener("click", this.hideBar);
  },
  unmounted() {
    document.removeEventListener("scroll", debounce(this.scrollEvent, 100));
    document.removeEventListener("click", this.hideBar);
  },
});
</script>

<style lang="scss" scoped>
@import "./styles/fontawesome/css/all.css";

.showBar-enter-active {
  animation: show-bar 0.1s ease-in;
}



@keyframes show-bar {
  0% {
   
    bottom: -20px;
  }
  100% {
 
    bottom: 0;
  }
}

@keyframes show-button {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>

