<template>
  <nav
    class="
      navbar
      container
      fixed-top
      bg-white
      justify-content-end
      pt-2
      px-md-2 px-lg-4
    "
  >
    <a
      :class="[
        'nav-item',
        'mx-4',
        selectedId == 'nav-item-1'  ? 'selected' : '',
      ]"
      id="nav-item-1"
      @click="selectItem"
    >
      {{$t("home")}}
    </a>

    <a
      :class="[
        'nav-item',
        'mx-4',
        selectedId == 'nav-item-2' ? 'selected' : '',
      ]"
      id="nav-item-2"
      @click="selectItem"
    >
     {{$t("aboutme")}}
      
    </a>

    <a
      :class="[
        'nav-item',
        'mx-4',
        selectedId == 'nav-item-3'? 'selected' : '',
      ]"
      id="nav-item-3"
      @click="selectItem"
    >
     {{$t("portfolio")}}
    
    </a>

    <div class="mx-4" @click="switchLang">
      <span class="lang selected">{{selectedLang}}</span>/<span class="lang">{{unselectdLang}}</span></div>
    <div class="col-1"></div>
  </nav>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  props:{
    navItemsHeight:Array,
  },
  data() {
    return {
      selectedId: "nav-item-1",
      unselectdLang:"汉",
      selectedLang:"En"
    };
  },
  methods: {
    selectItem(e: Event) {
      const eventTarget = e.target as HTMLElement;
      this.selectedId = eventTarget.id;
      // console.log(eventTarget.id);
  
      switch (this.selectedId) {
        case "nav-item-1":

          this.scrollTo(0, (this.navItemsHeight as Array<number>)[0]);
          break;
        case "nav-item-2":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[1]);
          break;
        case "nav-item-3":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[2]);
          break;
      }

      console.log(this.navItemsHeight)
 
    },
    scrollTo(x: number, y: number) {
      window.scrollTo(x, y);
    },
    switchLang(){
      const temp = this.selectedLang;
      this.selectedLang=this.unselectdLang;
      this.unselectdLang=temp;
      const lang =  this.selectedLang==="En"?"en":"zh"
      this.$i18n.locale=lang;
    }
  }
});
</script>


<style lang="scss" scoped>
@import "../styles/index";



span.lang{

    font-size:10px;
    font-weight: 600;
  &.selected{
    font-size:18px;
    font-weight: 800;
    color: #1c56ad;;
  }
  
  &:hover{
    cursor: pointer;
  }

}


a {
  text-decoration: none;
  color: $dark;
}


.nav-item {
  position: relative;
  font-weight: 600;
  font-size: 18px;

  &:hover {
    cursor: pointer;
  }
  &.selected {
    color: #1c56ad;
  }

  &.selected::before {
    position: absolute;
    content: "";
    width: 75%;
    height: 4px;
    background: #1c56ad;
    bottom: 0;
    top: 30px;
    text-align: right;
    left: 0%;
  }

  &.selected::before {
    animation: stretchLeft 0.3s ease-in;
  }

  &.selected::after {
    position: absolute;
    content: "";
    width: 75%;
    height: 4px;
    background: #1c56ad;
    bottom: 0;
    top: 30px;
    text-align: left;
    left: 25%;
  }

  &.selected::after {
    animation: stretchRight 0.3s ease-in;
  }
}

@keyframes stretchLeft {
  50% {
    left: 5%;
  }
}

@keyframes stretchRight {
  50% {
    left: 20%;
  }
}

</style>
