<template>
  <div class="fixed-bottom d-flex w-100 justify-content-around px-4 bottom-bar d-md-none">
    <div
      :class="['bottom-bar-item btn  col-3  px-0',  selectedId == 'nav-item-1' ? 'selected' : '']"
      @click="selectItem"
      id="nav-item-1"
    >
      <i class="fas fa-home"></i>
      <div class="bottom-bar-text">
        {{ $t("home") }}
      </div>
    </div>
    <div
      :class="['bottom-bar-item btn col-3 px-0',  selectedId == 'nav-item-2' ? 'selected' : '']"
      id="nav-item-2"
      @click="selectItem"
    >
      <i class="far fa-user"></i>
      <div class="bottom-bar-text">
        {{ $t("aboutme") }}
      </div>
    </div>
    <div
      :class="['bottom-bar-item btn  col-3  px-0', selectedId == 'nav-item-3' ? 'selected' : '']"
      @click="selectItem"
      id="nav-item-3"
    >
      <i class="far fa-images"></i>
      <div class="bottom-bar-text">
        {{ $t("portfolio") }}
      </div>
    </div>
  </div>
</template>

<script lang='ts'>
import { defineComponent } from "vue";
export default defineComponent({
  data() {
    return {
      selectedId: "nav-item-1",
    };
  },

  props: {
    navItemsHeight: Array,
  },
  methods: {
    selectItem(e: Event) {

      const eventTarget = e.currentTarget as HTMLElement;
      this.selectedId = eventTarget.id;
     
      switch (this.selectedId) {
        case "nav-item-1":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[0]);
          break;
        case "nav-item-2":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[1]);
          break;
        case "nav-item-3":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[2]);
          break;
      }
    },
    scrollTo(x: number, y: number) {
      window.scrollTo(x, y);
    },
  },

});
</script>

<style lang="scss" scoped>
@import "../styles/index.scss";

.fixed-bottom {
  box-shadow: 0px -4px 20px rgba(53, 75, 96, 0.1);
  margin: 0;
  height: 70px;
  align-items: center;
  background: #FFF9F0;

}

.bottom-bar-text {
  font-size: 10px;
  font-weight: 800;
}

.selected {
 
  & .bottom-bar-text {
  font-size: 12px;
  font-weight: 800;
  }


  color: #1c56ad !important;
}
</style>
