
import { defineComponent } from "vue";
export default defineComponent({
  data() {
    return {
      selectedId: "nav-item-1",
    };
  },

  props: {
    navItemsHeight: Array,
  },
  methods: {
    selectItem(e: Event) {

      const eventTarget = e.currentTarget as HTMLElement;
      this.selectedId = eventTarget.id;
     
      switch (this.selectedId) {
        case "nav-item-1":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[0]);
          break;
        case "nav-item-2":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[1]);
          break;
        case "nav-item-3":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[2]);
          break;
      }
    },
    scrollTo(x: number, y: number) {
      window.scrollTo(x, y);
    },
  },

});
