<template>
  <div
    class="
      fixed-bottom
      bg-white
      d-flex
      w-100
      px-4
      justify-content-end
      bottom-button
      d-md-none
    "
  >
    <div class="btn">
      <div class="bottom-button-text" @click="switchLang">
        <span class="lang selected">{{ selectedLang }}</span
        >/<span class="lang">{{ unselectdLang }}</span>
      </div>
    </div>

    <div class="btn" @click="showBottomBar">
      <div class="fas fa-th-large bottom-button-icon"></div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from "vue";
export default defineComponent({
  data() {
    return {
      unselectdLang: "汉",
      selectedLang: "En",
    };
  },
  methods: {
    showBottomBar: function (event) {
      event.stopPropagation();
      this.$emit("change-status");
    },
    switchLang() {
  
      const temp = this.selectedLang;
      this.selectedLang = this.unselectdLang;
      this.unselectdLang = temp;
      const lang = this.selectedLang === "En" ? "en" : "zh";
      this.$i18n.locale = lang;
    },
  },
});
</script>

<style lang="scss" scoped>
@import "../styles/index.scss";

.fixed-bottom {
  margin: 0;
  height: 70px;
  align-items: center;
  background: #FFF9F0;

}

span.lang {
  font-size: 10px;
  &.selected {
    font-size: 14px;
    font-weight: 800;
    color: #1c56ad;
  }

  &:hover {
    cursor: pointer;
  }
}

.bottom-button-text {
  color: $dark;
  font-size: 12px;
  font-weight: 800;
  display: inline-block;
  vertical-align: middle;
}

.bottom-button-icon {
  font-size: 20px;

  vertical-align: middle !important;
}
</style>
