
import { defineComponent } from "vue";

export default defineComponent({
  props:{
    navItemsHeight:Array,
  },
  data() {
    return {
      selectedId: "nav-item-1",
      unselectdLang:"汉",
      selectedLang:"En"
    };
  },
  methods: {
    selectItem(e: Event) {
      const eventTarget = e.target as HTMLElement;
      this.selectedId = eventTarget.id;
      // console.log(eventTarget.id);
  
      switch (this.selectedId) {
        case "nav-item-1":

          this.scrollTo(0, (this.navItemsHeight as Array<number>)[0]);
          break;
        case "nav-item-2":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[1]);
          break;
        case "nav-item-3":
          this.scrollTo(0, (this.navItemsHeight as Array<number>)[2]);
          break;
      }

      console.log(this.navItemsHeight)
 
    },
    scrollTo(x: number, y: number) {
      window.scrollTo(x, y);
    },
    switchLang(){
      const temp = this.selectedLang;
      this.selectedLang=this.unselectdLang;
      this.unselectdLang=temp;
      const lang =  this.selectedLang==="En"?"en":"zh"
      this.$i18n.locale=lang;
    }
  }
});
